*,*::before,*::after{
  margin:0;
  padding:0;
  box-sizing:border-box;
  vertical-align:baseline
}

body{
  background-color:#333;
  color:#ddd;
  font-family:"Roboto",sans-serif;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale
}

@media screen and (min-width: 768px){
  body{
    background-color:#fff
  }

  body::-webkit-scrollbar{
    display:block;
    max-width:1.5rem;
    height:1rem
  }

  body::-webkit-scrollbar-track{
    background:#ccc;
    border-top-left-radius:.3rem;
    border-bottom-left-radius:.3rem
  }

  body::-webkit-scrollbar-thumb{
    background:#454545;
    border-radius:.3rem;
    box-shadow:inset 2px 2px 2px rgba(97,97,97,.7),inset -2px -2px 2px rgba(97,97,97,.7)
  }
}

@media screen and (min-width: 3840px){
  html{
    font-size:32px
  }
}
.app-container{
  background-color:#fff;
  position:relative;
  padding:0
}

.app-desktop-menu{
  display:none
}

@media screen and (min-width: 768px){
  .app-container{
    display:grid;
    grid-template-columns:16rem 4fr;
    grid-template-areas:"desktop main" "desktop main"
  }

  .app-mobile-menu{
    display:none
  }

  .app-desktop-menu{
    display:block;
    grid-area:desktop
  }

  .app-main{
    grid-area:main;
    display:block
  }
}
.home-container{
  color:#333;
  padding:2rem 1rem;
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
  align-items:center;
  -webkit-justify-content:center;
  justify-content:center;
  -webkit-flex-direction:column;
  flex-direction:column
}

.home-container p{
  text-indent:2rem;
  white-space:pre-wrap
}

@media screen and (min-width: 768px){
  .home-container{
    padding-top:10rem
  }
}
.footer-container{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:space-between;
  justify-content:space-between;
  width:100%
}

.footer-icon{
  fill:#333
}

@media screen and (min-width: 768px){
  .footer-container{
    -webkit-justify-content:space-around;
    justify-content:space-around;
    padding:0 2rem
  }

  .footer-section{
    position:relative
  }

  .footer-section:hover::after{
    content:" ";
    position:absolute;
    top:-0.25rem;
    left:-0.25rem;
    width:2.5rem;
    height:2.5rem;
    border-radius:1.5rem;
    background-color:#454545;
    z-index:-1
  }

  .footer-icon{
    fill:#ccc;
    height:2rem;
    width:2rem
  }
}
.navbar-container{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
  flex-direction:column;
  width:100%;
  -webkit-justify-content:center;
  justify-content:center
}

.navbar-container a{
  margin-top:2rem;
  text-decoration:none;
  font-size:1.5rem;
  font-weight:500
}

@media screen and (min-width: 768px){
  .navbar-container a{
    padding-bottom:.2rem;
    width:-webkit-min-content;
    width:min-content;
    margin:1rem 0;
    color:#fff;
    background-size:0 2px,0 100%,0% 2px;
    background-position:top center,top center,bottom center
  }

  .navbar-container a:active{
    color:#ff4500
  }

  .navbar-container a:visited{
    color:#ddd
  }

  .navbar-container a::after{
    display:block;
    content:"";
    border-bottom:solid 1px #ff4500;
    -webkit-transform:scaleX(0);
    transform:scaleX(0);
    transition:-webkit-transform 300ms ease-in-out;
    transition:transform 300ms ease-in-out;
    transition:transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
    -webkit-transform-origin:100% 50%;
    transform-origin:100% 50%
  }

  .navbar-container a:hover{
    color:coral;
    -webkit-animation:border-up 1s linear;
    animation:border-up 1s linear
  }

  .navbar-container a:hover::after{
    -webkit-transform:scaleX(1);
    transform:scaleX(1);
    -webkit-transform-origin:0% 50%;
    transform-origin:0% 50%
  }
}
.sidebar-container{
  position:fixed;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
  flex-direction:column;
  -webkit-justify-content:space-between;
  justify-content:space-between;
  -webkit-align-items:center;
  align-items:center;
  left:0;
  top:0;
  width:220px;
  background-color:#ccc;
  opacity:.9;
  padding:7rem 1rem;
  height:100vh;
  z-index:10;
  border-bottom-right-radius:3px
}

.sidebar-container::before{
  content:" ";
  z-index:-1;
  background-color:#e0e0e0;
  opacity:.8;
  width:220px;
  left:0;
  top:0;
  position:absolute;
  height:100vh;
  -webkit-filter:blur(7px);
  filter:blur(7px)
}

.sidebar-container a{
  color:#333
}

.sidebar-container a:visited{
  color:#333
}

.sidebar-container a:active{
  color:#ff4500
}

.sidebar-in{
  -webkit-animation:trans 1.2s cubic-bezier(0.02, 0.5, 0.03, 1) forwards;
  animation:trans 1.2s cubic-bezier(0.02, 0.5, 0.03, 1) forwards
}

.sidebar-out{
  -webkit-animation:transback 1s cubic-bezier(0.2, 0.2, 0.1, 0.8) forwards;
  animation:transback 1s cubic-bezier(0.2, 0.2, 0.1, 0.8) forwards
}

@-webkit-keyframes transback{
  from{
    -webkit-transform:translateX(0);
    transform:translateX(0)
  }

  to{
    -webkit-transform:translateX(-400px);
    transform:translateX(-400px)
  }
}

@keyframes transback{
  from{
    -webkit-transform:translateX(0);
    transform:translateX(0)
  }

  to{
    -webkit-transform:translateX(-400px);
    transform:translateX(-400px)
  }
}

@-webkit-keyframes trans{
  from{
    -webkit-transform:translateX(-400px);
    transform:translateX(-400px)
  }

  to{
    -webkit-transform:translateX(0);
    transform:translateX(0)
  }
}

@keyframes trans{
  from{
    -webkit-transform:translateX(-400px);
    transform:translateX(-400px)
  }

  to{
    -webkit-transform:translateX(0);
    transform:translateX(0)
  }
}
